<template>
    <div class="container">
        <div class="portlet bordered">
            <div class="portlet-title">
                <div class="caption">
                    <i class="el-icon-setting font-red"></i>
                    <span class="blod font-red"></span>
                </div>
            </div>
            <div class="portlet-body">
                <div class="table-toolbar">
                    <el-form :inline="true" :model="filters" class="demo-form-inline">
                        <el-form-item>
                            <el-input v-model="filters.name" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" v-on:click="getUsers"><i class="el-icon-search el-icon--right"></i>
                            </el-button>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="success" @click="handleAdd"><i class="el-icon-plus el-icon--right"></i>
                            </el-button>
                        </el-form-item>
                    </el-form>

                </div>
                <div class="table-body">
                    <!---->
                    <el-table
                            @sort-change="sortChange"
                            :data="tableData"
                            border
                            style="width: 100%">
                        <el-table-column
                                sortable="custom"
                                align="center"
                                prop="id"
                                label=""
                                width="100">
                        </el-table-column>
                        <el-table-column
                                sortable="custom"
                                align="center"
                                prop="name"
                                label=""
                                width="150">
                        </el-table-column>
                        <el-table-column
                                sortable="custom"
                                :formatter="formatSex"
                                align="center"
                                width="100"
                                prop="gender"
                                label="">
                        </el-table-column>
                        <el-table-column
                                sortable="custom"
                                align="center"
                                prop="nickname"
                                label=""
                                width="150">
                        </el-table-column>
                        <el-table-column
                                sortable="custom"
                                align="center"
                                prop="phoneNumber"
                                label="">
                        </el-table-column>
                        <el-table-column
                                sortable="custom"
                                align="center"
                                prop="email"
                                label="">
                        </el-table-column>
                        <el-table-column
                                :formatter="formatDate"
                                sortable="custom"
                                align="center"
                                prop="createDate"
                 width="135"
                                label="">
                            <!--<div>{{ props.row.createDate | moment('YYYYMMDD HH:mm:ss')}}</div>-->
                        </el-table-column>
                        <el-table-column
                                sortable="custom"
                                width="100"
                                align="center"
                                :formatter="formatStatus"
                                prop="state"
                                label="">
                        </el-table-column>
                        <el-table-column
                                sortable="custom"
                                width="200"
                                align="center"
                                prop="description"
                                label="">
                        </el-table-column>
                        <el-table-column
                                align="center"
                                prop="edit"
                                label=""
                                width="100">
                            <template slot-scope="scope">
                                <el-button size="small" @click="handleEdit(scope.$index, scope.row)" type="primary">
                                </el-button>
                            </template>
                        </el-table-column>
                        <el-table-column
                                align="center"
                                prop="delete"
                                label=""
                                width="100">
                            <template slot-scope="scope">
                                <el-button size="small" @click="deleteRow(scope.$index, scope.row)"
                                           type="danger">
                                </el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <!---->
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-sizes="[5, 10, 15, 20]"
                            :page-size="pageSize"
                            layout="total, sizes, prev, pager, next, jumper"
                            :total="totalPages">
                    </el-pagination>

                    <!---->
                    <el-dialog title="" :visible.sync="editFormVisible">
                        <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
                            <el-form-item label="" prop="name">
                                <el-input v-model="editForm.name" auto-complete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="">
                                <el-radio-group v-model="editForm.gender">
                                    <el-radio class="radio" :label="'male'"></el-radio>
                                    <el-radio class="radio" :label="'female'"></el-radio>
                                </el-radio-group>
                            </el-form-item>
                            <el-form-item label="">
                                <el-input v-model="editForm.nickname" auto-complete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="">
                                <el-input v-model="editForm.phoneNumber"></el-input>
                            </el-form-item>
                            <el-form-item
                                    prop="email"
                                    label=""
                                    :rules="editFormRules">
                                <el-input v-model="editForm.email"></el-input>
                            </el-form-item>
                            <el-form-item label="">
                                <el-date-picker type="date" placeholder="" v-model="editForm.date"></el-date-picker>
                            </el-form-item>
                            <el-form-item label="">
                                <el-select v-model="editForm.state" placeholder="">
                                    <el-option label="" value="enable"></el-option>
                                    <el-option label="" value="disable"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="">
                                <el-input type="textarea" v-model="editForm.description"></el-input>
                            </el-form-item>
                        </el-form>
                        <div slot="footer" class="dialog-footer">
                            <el-button @click="editFormVisible = false"> </el-button>
                            <el-button type="primary" @click.native="editSubmit"> </el-button>
                        </div>
                    </el-dialog>

                    <!---->
                    <el-dialog title="" :visible.sync="addFormVisible">
                        <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
                            <el-form-item label="" prop="name">
                                <el-input v-model="addForm.name" auto-complete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="">
                                <el-radio-group v-model="addForm.gender">
                                    <el-radio class="radio" :label="'male'"></el-radio>
                                    <el-radio class="radio" :label="'female'"></el-radio>
                                </el-radio-group>
                            </el-form-item>
                            <el-form-item label="">
                                <el-input v-model="addForm.nickname" auto-complete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="">
                                <el-input v-model="addForm.phoneNumber"></el-input>
                            </el-form-item>
                            <el-form-item
                                    prop="email"
                                    label="">
                                <el-input v-model="addForm.email"></el-input>
                            </el-form-item>
                            <!--<el-form-item label="">-->
                                <!--<el-date-picker type="date" placeholder="" v-model="addForm.date"></el-date-picker>-->
                            <!--</el-form-item>-->
                            <el-form-item label="">
                                <el-select v-model="addForm.state" placeholder="">
                                    <el-option label="" value="enable"></el-option>
                                    <el-option label="" value="disable"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="">
                                <el-input type="textarea" v-model="addForm.description"></el-input>
                            </el-form-item>
                        </el-form>
                        <div slot="footer" class="dialog-footer">
                            <el-button @click="addFormVisible = false"> </el-button>
                            <el-button type="primary" @click="addSubmit" > </el-button>
                        </div>
                    </el-dialog>


                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import * as api from "../api/api"
    import * as dataFormat from '../common/Moment';
    import * as RestCode from "../common/RestCode"

    export default {
        name: "UserPage",
        data() {
            return {
                // 
                tableData: [],
                // toolbar
                filters: {
                    name: '',

                },
                currentPage: 1,// 
                pageSize: 5, // 
                totalPages: 5,
                orderColumn: "id",
                //ascending, descending
                order: "ascending",

                editFormVisible: false,//
                editFormRules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    email: [{required: true, message: '', trigger: 'blur'},
                        {type: 'email', message: '', trigger: ['blur', 'change']}]
                },
                //
                editForm: {
                    id: 0,
                    name: '',
                    gender: 'male',
                    nickname: '',
                    phoneNumber: '',
                    email: '',
                    createDate: '',
                    state: 'disable',
                    description: '',
                },
                addFormVisible: false,//
                addFormRules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    email: [{required: true, message: '', trigger: 'blur'},
                        {type: 'email', message: '', trigger: ['blur', 'change']}]
                },
                // 
                addForm: {
                    // id: 1,
                    name: '',
                    gender: 'male',
                    nickname: '',
                    phoneNumber: '',
                    email: '',
                    // createDate: '',
                    state: 'disable',
                    description: '',
                },

            }
        },
        methods: {
            //
            formatSex: function (row) {
                // console.log("sex===" + row.sex)
                return row.gender == 'male' ? '' : row.gender == 'female' ? '' : '';
            },
            //
            formatStatus: function (row) {
                return row.state == 'enable' ? '' : row.state == 'disable' ? '' : '';
            },

            formatDate: function (row, column, cellValue) {
                // console.log(row, column, cellValue, index)
                return dataFormat.formatTimeToStr(cellValue, 'yyyyMMdd hh:mm:ss')
            },
            // 
            handleSizeChange(val) {
                // console.log(` ${val} `);
                this.pageSize = val;
                this.getUsers();
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.getUsers();
            },
            sortChange: function (column) {
                // console.log("column", column, column.prop, column.order)
                this.orderColumn = column.prop;
                this.order = column.order;
                // console.log("data", this.orderColumn, this.order)
                this.getUsers()
            },
            //
            getUsers() {
                let params = new URLSearchParams();
                params.append('currentPage', this.currentPage - 1);
                params.append('pageSize', this.pageSize);
                params.append('orderColumn', this.orderColumn);
                params.append('order', this.order === 'ascending' ? 'asc' : 'desc');
                params.append('userName', this.filters.name);
                api.userList(params).then((res) => {
                    // console.log(this.totalPages)
                    this.totalPages = res.data.data.totalElements;
                    this.tableData = res.data.data.content;
                    // console.log("tableData", this.tableData)
                    //NProgress.done();
                    // console.log(this.totalPages)
                });
            },

            // 
            handleAdd: function () {
                this.addFormVisible = true;
            }
            ,
            // 
            addSubmit: function () {
                // console.log(this.addForm)
                // this.tableData.push(this.addForm)

                    // console.log(this.addForm)
                this.$refs.addForm.validate((valid) => {
                    if (valid) {
                        api.requestRegister(this.addForm).then((res) => {
                            // console.log(this.addForm)
                            let code = res.code;
                            // let msg = res.msg;
                            if (code === RestCode.SUCCESS) {
                                this.$message.success('');
                                this.addForm = {};
                            }
                            else if (code === RestCode.RECORD_ALREADY_EXISTS) {
                                this.$message.warning('');
                            }
                            else {
                                this.$message.error('');
                            }
                        });
                        this.addFormVisible = false;
                        // this.$refs['addForm'].resetFields();
                        this.getUsers();
                        // });

                    }
                });
            },

            // 
            deleteRow: function (index, row) {
                // console.log(index,row)
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    api.removeUser(row.userId).then((res) => {
                        // console.log(res)
                        let code = res.data.code;
                        // console.log(code)
                        if (code === RestCode.SUCCESS) {
                            this.$message.success('!');
                            this.getUsers();
                        } else if (code === RestCode.RECORD_NOT_FOUND) {
                            this.$message.warning('!');
                        }
                        else if (code === RestCode.FAIL) {
                            this.$message.error('!');
                        }
                        else {
                            this.$message.info('!');
                        }

                    });
                }).catch(() => {
                    this.$message.info('!');
                });

            },


            // 
            handleEdit: function (index, row) {
                this.editFormVisible = true;
                this.editForm = Object.assign({}, row);
            }
            ,
            //
            editSubmit: function () {
                this.$refs.editForm.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            let para = Object.assign({}, this.editForm);
                            api.editUser(para).then((res) => {
                                // console.log(res)
                                this.$message.success('');
                                this.$refs['editForm'].resetFields();
                                this.editFormVisible = false;
                                this.getUsers();
                            });
                        });

                    }
                });
            },
        },
        mounted: function () {
            this.getUsers();
        }
    }

</script>

<style scoped>
    .container {
        padding: 0 15px;
    }

    .portlet {
        margin-top: 0;
        padding: 0;
        margin-bottom: 25px;
    }

    .bordered {
        border: 1px solid #e7ecf1 !important;
    }

    .portlet-title {
        padding: 17px 20px 10px;
        margin-bottom: 0;
        border-bottom: 1px solid #e7ecf1;
        min-height: 48px;
    }

    .caption {
        padding: 10px 0;
        float: left;
        font-size: 18px;
        display: inline-block;
        line-height: 18px;
    }

    .caption::after {
        clear: both;
        content: "";
        display: table;
    }

    .caption i {
        margin-right: 5px;
        font-weight: 300;
    }

    .blod {
        font-weight: 700 !important;
        font-size: 16px;
    }

    .font-red {
        color: red;
    }

    .portlet-body {
        padding: 10px 20px 0;
        margin-bottom: 20px;
    }

    .table-toolbar {
        margin-bottom: 20px;
        margin-top: 20px;
        padding-top: 10px;
    }

    .table-body {
        margin: 0 10px 20px;
    }

    .el-pagination {
        margin-top: 20px;
        padding: 10px 10px;
        text-align: right;
    }

</style>
