import Login from './system/Login'
import Register from './system/Register'
import NotFound from './system/404'
import Home from './components/Home'
import Checkin from './page/Checkin'
import Reserve from './page/Reserve'
import CheckOut from  './page/CheckOut'
import OnlineCategory from './page/OnlineCategory'
import UserPage from './page/UserPage'
import RoomPage from './page/RoomPage'
import RoomCategory from './page/RoomCategory'
import CustomerPage from './page/CustomerPage'



const routes = [
    {
        path: '/login',
        component: Login,
    }, {
        path: '/register',
        component: Register,
    }, {
        path: '/404',
        component: NotFound,
    },
    // {
    //     path: '/home',
    //     component: Home,
    // },
    {
        path: '/home',
        component: Home,
        children: [
            {path: 'checkin', component: Checkin, name: ''},
            {path: 'reserve', component: Reserve, name: ''},
            {path: 'checkOut', component: CheckOut, name: ''},
            {path: 'onlineCategory', component: OnlineCategory, name: ''},
            {path: 'userPage', component: UserPage, name: ''},
            {path: 'roomPage', component: RoomPage, name: ''},
            {path: 'roomCategory', component: RoomCategory, name: ''},
            {path: 'customerPage', component: CustomerPage, name: ''},
            // { path: '/form', component: Form, name: 'Form' },
            // { path: '/user', component: user, name: '' },
        ]
    },
]


export default routes;