<template>
    <div class="container">
        <h3 class="portlet-title">
            
            <small></small>
        </h3>
        <div class="portlet-body">
            <div class="form-body">
                <el-form ref="form"
                         :inline="true"
                         :model="form"
                         labelPosition="left">
                    <el-form-item>
                        <el-input v-model="form.tel" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" v-on:click="getUsers"></el-button>
                    </el-form-item>
                </el-form>
            </div>
            <div class="table-body">
                <!---->
                <el-table
                        :data="reserveTable"
                        border
                        style="width: 100%">
                    <el-table-column
                            align="center"
                            prop="id"
                            label="">
                    </el-table-column>
                    <el-table-column
                            align="center"
                            prop="name"
                            label=""
                            width="150">
                    </el-table-column>
                    <el-table-column
                            align="center"
                            prop="nickname"
                            label=""
                            width="150">
                    </el-table-column>
                    <el-table-column
                            align="center"
                            prop="phoneNumber"
                            label="">
                    </el-table-column>
                    <el-table-column
                            align="center"
                            prop="email"
                            label="">
                    </el-table-column>
                    <el-table-column
                            align="center"
                            prop="room"
                            label="">
                    </el-table-column>
                    <el-table-column
                            align="center"
                            prop="roomNumber"
                            label="">
                    </el-table-column>
                    <el-table-column
                            align="center"
                            prop="data"
                            label="">
                    </el-table-column>
                    <el-table-column
                            align="center"
                            prop="prices"
                            label="">
                    </el-table-column>
                    <el-table-column
                            align="center"
                            prop="operation"
                            label=""
                            width="120">
                        <template slot-scope="scope">
                            <el-button size="small" @click="handleEdit(scope.$index, scope.row)" type="primary">
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <!---->
                <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-sizes="[5, 10, 15, 20]"
                        :page-size="pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="totalPages">
                </el-pagination>
            </div>
        </div>

    </div>
</template>

<script>
    import * as api from "../api/api"
    export default {
        name: "Reserve",
        data() {
            return {
                form: {
                    tel: '',
                },
                reserveTable:[],
                currentPage: 1,// 
                pageSize: 5, // 
                totalPages: 5,
            }
        },
        methods: {
            //
            getUsers() {
                let params = new URLSearchParams();
                params.append('currentPage', this.currentPage - 1);
                params.append('pageSize', this.pageSize);
                params.append('orderColumn', this.orderColumn);
                params.append('order', this.order === 'ascending' ? 'asc' : 'desc');
                params.append('userName', this.filters.name);
                api.userList(params).then((res) => {
                    // console.log(this.totalPages)
                    this.totalPages = res.data.data.totalElements;
                    this.tableData = res.data.data.content;
                    // console.log("tableData", this.tableData)
                    //NProgress.done();
                    // console.log(this.totalPages)
                });
            },
            // 
            handleSizeChange(val) {
                // console.log(` ${val} `);
                this.pageSize = val;
                this.getUsers();
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.getUsers();
            },
            sortChange: function (column) {
                // console.log("column", column, column.prop, column.order)
                this.orderColumn = column.prop;
                this.order = column.order;
                // console.log("data", this.orderColumn, this.order)
                this.getUsers()
            },
        },
        // mounted: function () {
        //     this.getUsers();
        // }
    }
</script>

<style scoped>
    .container {
        border: 1px solid rgb(231, 236, 241);
        padding: 12px 20px 15px;
        margin-top: 0;
    }

    .portlet-title {
        border-bottom: 1px solid rgb(238, 241, 245);
        padding: 0 0 10px 0;
        margin: 25px 0;
        font-size: 24px;
        font-weight: 300;
        color: #666;
        letter-spacing: -1px;
        display: block;
    }

    .table-body {
        overflow: hidden;
    }

    .el-pagination {
        float: right;
        margin-top: 20px;
    }


</style>